
// src/components/ExperienceDetail.tsx
'use client';

import { useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import {
  ArrowLeft,
  Download,
  ExternalLink,
  Car,
  Video,
  Briefcase
} from 'lucide-react';
import { ExperienceData } from '@/data/experiences';

interface ExperienceDetailProps {
  experience: ExperienceData;
  onBack: () => void;
}

const iconMap = {
  Car: Car,
  Video: Video,
  Briefcase: Briefcase
};

export default function ExperienceDetail({ experience, onBack }: ExperienceDetailProps) {
  const IconComponent = iconMap[experience.icon as keyof typeof iconMap] || Briefcase;

  useEffect(() => {
    // Scrolls to top when component mounts or project changes
    window.scrollTo(0, 0);
  }, [experience]); // Add experience as a dependency

  return (
    <div className="w-full">
      {/* Header for ExperienceDetail - sticky within its own scroll context */}
      <div className="sticky top-0 z-40 bg-white/90 backdrop-blur-md border-b border-slate-200">
        <div className="container mx-auto px-3 py-2 sm:px-4 sm:py-3"> {/* Reduced padding */}
          <div className="flex items-center space-x-2 sm:space-x-3"> {/* Reduced spacing */}
            <Button
              variant="ghost"
              size="sm"
              onClick={onBack}
              className="hover:bg-slate-100 text-slate-700 whitespace-nowrap" // Added whitespace-nowrap to prevent button text wrapping
            >
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back to Experiences
            </Button>
            <div className="h-4 w-px bg-slate-300 shrink-0" /> {/* Added shrink-0 */}
            <div className="flex items-center space-x-2 sm:space-x-3 overflow-hidden"> {/* Added overflow-hidden for title safety */}
              <div className={`p-1 rounded-lg bg-gradient-to-r ${experience.gradient} bg-opacity-20 shadow-inner shrink-0`}> {/* Reduced padding, added shrink-0 */}
                {IconComponent && <IconComponent className="h-4 w-4 text-slate-700" />} {/* Consistent small icon size */}
              </div>
              <div className="overflow-hidden"> {/* Added overflow-hidden for text safety */}
                <h1 className="text-sm font-semibold sm:text-base text-slate-800 truncate">{experience.title}</h1> {/* Reduced font size, added truncate */}
                <p className="text-xs text-slate-600 truncate">{experience.company}</p> {/* Kept small, added truncate */}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container mx-auto px-6 py-8">
        {/* Hero Section */}
        <Card className="border-0 shadow-xl bg-gradient-to-r from-white to-blue-50/50 overflow-hidden mb-8">
          <div className={`h-2 bg-gradient-to-r ${experience.gradient}`} />
          <CardHeader>
            <div className="flex flex-col lg:flex-row lg:items-start lg:justify-between gap-6">
              <div className="flex-1">
                <div className="flex items-center space-x-3 mb-4">
                  <div className={`p-3 rounded-lg bg-gradient-to-r ${experience.gradient} bg-opacity-20 shadow-inner`}>
                    {IconComponent && <IconComponent className="h-8 w-8 text-slate-700" />}
                  </div>
                  <div>
                    <CardTitle className="text-3xl font-bold text-slate-800">
                      {experience.title}
                    </CardTitle>
                    <CardDescription className="text-lg text-slate-600">
                      {experience.company}
                    </CardDescription>
                  </div>
                </div>

                <div className="flex flex-wrap gap-2 mb-6">
                  {experience.tags.map((tag, index) => (
                    <Badge key={index} variant="outline" className="text-xs">
                      {tag}
                    </Badge>
                  ))}
                </div>

                <p className="text-lg text-slate-700 leading-relaxed">
                  {experience.description}
                </p>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col space-y-3 lg:min-w-[200px] pt-2">
                {experience.more?.website && (
                  <Button
                    variant="outline"
                    onClick={() => window.open(experience.more?.website, '_blank')}
                    className="hover:bg-slate-50 shadow hover:shadow-md"
                  >
                    <ExternalLink className="mr-2 h-4 w-4" />
                    Visit Website
                  </Button>
                )}
              </div>
            </div>
          </CardHeader>
        </Card>

        {/* Main Content */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Content */}
          <div className="lg:col-span-2">
            <Card className="border-0 shadow-lg">
              <CardHeader>
                <CardTitle className="text-xl">Experience Details</CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="list-disc list-inside space-y-2 text-base text-slate-700 leading-relaxed">
                  {experience.details.map((detail, index) => (
                    <li key={index}>{detail}</li>
                  ))}
                </ul>
                {experience.more?.companyIntro && (
                  <div className="mt-6">
                    <h3 className="text-lg font-semibold text-slate-800 mb-3">About {experience.company}</h3>
                    <p className="text-slate-700 leading-relaxed">{experience.more.companyIntro}</p>
                  </div>
                )}
                 {experience.more?.challenges && experience.more.challenges.length > 0 && (
                  <div className="mt-6">
                    <h3 className="text-lg font-semibold text-slate-800 mb-3">Challenges</h3>
                    <ul className="list-disc list-inside space-y-2 text-base text-slate-700 leading-relaxed">
                      {experience.more.challenges.map((challenge, index) => (
                        <li key={index}>{challenge}</li>
                      ))}
                    </ul>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Sidebar */}
          <div className="space-y-6">

            <Card className="border-0 shadow-lg">
              <CardHeader>
                <CardTitle className="text-lg">Technologies Used</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="flex flex-wrap gap-2">
                  {experience.tags.map((tag, index) => (
                    <Badge key={index} variant="outline" className="text-sm">
                      {tag}
                    </Badge>
                  ))}
                </div>
              </CardContent>
            </Card>

          </div>
        </div>
      </div>
    </div>
  );
}
